body {
  margin: 0;
  font-family: 'Mitr-light', 'Mitr';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}



/* mitr-200 - latin_thai */

@font-face {
  font-family: 'Mitr-thin';
  font-style: normal;
  font-weight: 200;
  src: url('./assets/fonts/mitr-v4-latin_thai-200.eot');
  /* IE9 Compat Modes */
  src: local('Mitr ExtraLight'), local('Mitr-ExtraLight'), url('./assets/fonts/mitr-v4-latin_thai-200.eot?#iefix') format('embedded-opentype'),
    /* IE6-IE8 */
    url('./assets/fonts/mitr-v4-latin_thai-200.woff2') format('woff2'),
    /* Super Modern Browsers */
    url('./assets/fonts/mitr-v4-latin_thai-200.woff') format('woff'),
    /* Modern Browsers */
    url('./assets/fonts/mitr-v4-latin_thai-200.ttf') format('truetype'),
    /* Safari, Android, iOS */
    url('./assets/fonts/mitr-v4-latin_thai-200.svg#Mitr') format('svg');
  /* Legacy iOS */
}

/* mitr-300 - thai_latin */

@font-face {
  font-family: "Mitr-light";
  font-style: normal;
  font-weight: 300;
  src: url("./assets/fonts/mitr-v4-thai_latin-300.eot");
  /* IE9 Compat Modes */
  src: local("Mitr Light"), local("Mitr-Light"), url("./assets/fonts/mitr-v4-thai_latin-300.eot?#iefix") format("embedded-opentype"),
    /* IE6-IE8 */
    url("./assets/fonts/mitr-v4-thai_latin-300.woff2") format("woff2"),
    /* Super Modern Browsers */
    url("./assets/fonts/mitr-v4-thai_latin-300.woff") format("woff"),
    /* Modern Browsers */
    url("./assets/fonts/mitr-v4-thai_latin-300.ttf") format("truetype"),
    /* Safari, Android, iOS */
    url("./assets/fonts/mitr-v4-thai_latin-300.svg#Mitr") format("svg");
  /* Legacy iOS */
}

/* mitr-regular - thai_latin */

@font-face {
  font-family: "Mitr";
  font-style: bold;
  font-weight: 400;
  src: url("./assets/fonts/mitr-v4-thai_latin-regular.eot");
  /* IE9 Compat Modes */
  src: local("Mitr Regular"), local("Mitr-Regular"), url("./assets/fonts/mitr-v4-thai_latin-regular.eot?#iefix") format("embedded-opentype"),
    /* IE6-IE8 */
    url("./assets/fonts/mitr-v4-thai_latin-regular.woff2") format("woff2"),
    /* Super Modern Browsers */
    url("./assets/fonts/mitr-v4-thai_latin-regular.woff") format("woff"),
    /* Modern Browsers */
    url("./assets/fonts/mitr-v4-thai_latin-regular.ttf") format("truetype"),
    /* Safari, Android, iOS */
    url("./assets/fonts/mitr-v4-thai_latin-regular.svg#Mitr") format("svg");
  /* Legacy iOS */
}