.AddQuestionWrapper {

    margin: 5px auto;
    width: 95%;
    background: rgba(0, 0, 0, 0.5);
    padding: 15px 5px;
    padding-bottom: 15px;
    color: white;
    box-sizing: border-box;
}

.AddQuestionWrapper>h3 {
    text-align: center;
    margin: 0
}

.AddQuestionWrapper>input {
    font-family: 'Mitr';
    padding: 0 5px;
    box-sizing: border-box;
    background: rgba(255, 255, 255, 0.5);
    height: 30px;
    border: none;
    width: 100%;
    margin: 0;
    font-size: 16px;
    margin-top: 20px
}

.AddQuestionWrapper>button {
    width: 100%;
    background: #e62b1e;
    height: 30px;
    font-family: 'Mitr';
    font-size: 0.9em;
    color: white;
    text-align: center;
    padding: 2px;
    border: 0;
    text-decoration: none;
    border-radius: 20px;
    margin: auto;
    margin-top: 25px;
}

.AddQuestionWrapper>button:focus {

    outline: -webkit-focus-ring-color auto 0px;
}