.Answer {
    width: 80%;
    background: rgba(255, 255, 255, 0.25);
    padding: 5px 5px 20px 5px;
    margin-bottom: 20px;
    margin-left: auto;
    margin-right: 5px;

}

.Answer>p:after {
    content: '';
    margin-top: -20px;
    padding-top: 0px;
    position: relative;
    bottom: -35px;
    left: 90%;
    border-width: 15px 0 0 15px;
    border-style: solid;
    border-color: rgba(255, 255, 255, 0.25) transparent;
    display: block;
    width: 0;
}

.Answer>p {
    margin: 0;


}

.Answer>h3 {
    margin: 0
}

.Answer>small {

    margin: 0;
    margin-top: 5px;
    font-size: 0.6em;
    float: right;
}