.Question {

    margin: 5px;
    width: 85%;
    background: rgba(0, 0, 0, 0.3);
    padding: 5px 5px;
    padding-bottom: 20px;
    margin-bottom: 23px;
    color: white;

}

.Question>p:after {
    content: '';
    margin-top: -20px;
    padding-top: 0px;
    position: relative;
    bottom: -35px;
    left: 15px;
    border-width: 15px 15px 0 0;
    border-style: solid;
    border-color: rgba(0, 0, 0, 0.3) transparent;
    display: block;
    width: 0;
}

.Question>p {
    margin: 0
}

.Question>h3 {
    margin: 0
}