@keyframes bouceIn {
    0% {
        transform: scale(0);
        opacity: 0;
    }

    90% {
        transform: scale(1.05)
    }



    100% {
        opacity: 1;
        transform: scale(1.0)
    }
}

@keyframes bouceOut {
    0% {
        transform: scale(1);
        opacity: 1;
    }

    10% {
        transform: scale(1.05)
    }



    100% {
        opacity: 0;
        transform: scale(0)
    }
}


.in-enter {}

.in-enter-active {
    animation: bouceIn 0.3s cubic-bezier(0.4, 0.0, 0.2, 1) forwards;
}

.in-exit-active {
    animation: bouceOut 0.3s cubic-bezier(0.4, 0.0, 0.2, 1) forwards;
}

.in-exit {}