.FooterBtnQ {
    width: 80%;
    background: #e62b1e;
    color: white;
    text-align: center;

    padding: 2px;
    border-radius: 20px;
    margin: auto;
    transition: all .3s ease-out;
}

.FooterInput {
    width: 70%;
    margin-left: 5px;
    align-self: center;
    background: rgba(255, 255, 255, 0.5);
    border: none;
    height: 25px;
    font-family: 'Mitr';
    font-size: 16px;
    padding: 0 5px;
}

.InputWrapper {

    align-items: center;
    display: flex;
    width: 100%;
    height: 100%;

}

.InputWrapper>button {

    font-family: 'Mitr';
    width: 20%;
    background: #e62b1e;
    color: white;
    border: none;
    height: 25px;
    margin-left: 20px;
}