.App {
    text-align: center;
}

/* css-background  animation */
.area {
    position: fixed;
    left: 0;
    top: 0;
    z-index: -100;
    background: #b4150a;
    background: -webkit-linear-gradient(to right, #d42d21, #b4150a);
    background: linear-gradient(to right, #d42d21, #b4150a);
    width: 100vw;


}

.ModalDetail {
    font-size: 0.9em;
    margin: 0;
}

.ModalBtn>p {

    margin: 0;
}

.ModalBtn {
    align-items: center;
    margin: 0;
}

.ModalBtn>p:first-child {
    margin: 0;
    font-size: 0.8em;
    font-family: "Mitr";

}

.ModalBtn>p:last-child {
    margin: 0;
    font-family: "Mitr";
    color: #b4150a;
}

.routewrapper {
    position: relative;
    width: 100vw;
}

.routewrapper>div {
    position: absolute;
    width: 100vw;
    margin-top: 60px;

}

.circles {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.circles li {
    position: absolute;
    display: block;
    list-style: none;
    width: 20px;
    height: 20px;
    background: rgba(255, 255, 255, 0.2);
    animation: animate 25s linear infinite;
    bottom: -150px;

}

.circles li:nth-child(1) {
    left: 25%;
    width: 50px;
    height: 50px;
    animation-delay: 0s;
}


.circles li:nth-child(2) {
    left: 10%;
    width: 10px;
    height: 10px;
    animation-delay: 2s;
    animation-duration: 10s;
}

.circles li:nth-child(3) {
    left: 64%;
    width: 15px;
    height: 15px;
    animation-delay: 5s;
}

.circles li:nth-child(4) {
    left: 40%;
    width: 50px;
    height: 50px;
    animation-delay: 0s;
    animation-duration: 10s;
}

.circles li:nth-child(5) {
    left: 55%;
    width: 15px;
    height: 15px;
    animation-delay: 0s;
}

.circles li:nth-child(6) {
    left: 60%;
    width: 70px;
    height: 70px;
    animation-delay: 3s;
}

.circles li:nth-child(7) {
    left: 20%;
    width: 100px;
    height: 100px;
    animation-delay: 7s;
}

.circles li:nth-child(8) {
    left: 40%;
    width: 18px;
    height: 18px;
    animation-delay: 15s;
    animation-duration: 45s;
}

.circles li:nth-child(9) {
    left: 10%;
    width: 7px;
    height: 7px;
    animation-delay: 2s;
    animation-duration: 35s;
}

.circles li:nth-child(10) {
    left: 70%;
    width: 100px;
    height: 100px;
    animation-delay: 0s;
    animation-duration: 11s;
}



@keyframes animate {

    0% {
        transform: translateY(0) rotate(0deg);
        opacity: 1;
        border-radius: 0;
    }

    100% {
        transform: translateY(-1000px) rotate(720deg);
        opacity: 0;
        border-radius: 50%;
    }

}