.Footer {
    position: fixed !important;
    bottom: 0px;
    left: 0;
    width: 100%;
    height: 46px;
    background: rgba(0, 0, 0, 0.8);
    display: flex;
    justify-content: center;
    opacity: 0;
    transform: translateY(100%);
    transition: all 2s cubic-bezier(.26, 1.07, .04, .91);
    transition-delay: 100ms;
}

.FooterIn {
    opacity: 1;
    transform: translateY(0);

}