.Toolbar {
    position: fixed;
    top: 0;
    left: 0;
    background: #000000;
    width: 100%;
    height: 48px;
    z-index: 100;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.Toolbar>div {
    display: flex;
}

.open:before {
    background-color: #94E185;
    border-color: #78D965;
    box-shadow: 0px 0px 4px 1px #94E185;
}

.dead:before {
    background-color: #C9404D;
    border-color: #C42C3B;
    box-shadow: 0px 0px 4px 1px #C9404D;
}

.status:before {
    content: ' ';
    display: inline-block;
    width: 7px;
    height: 7px;

    border-radius: 7px;
}

.status {
    align-self: center;
    margin-right: 10px;
    margin-bottom: 3px;
}